.heart {
  color: rgba(0, 0, 0, 0);
  background-image: linear-gradient(#0c54a0 50%, #ffed00 50%);
  background-clip: text;
  -webkit-background-clip: text;
  margin-left: 3px;
  font-size: 25px;
  animation: heartbeat 1s infinite;
  display: block;
}

@keyframes heartbeat {
  0% {
    transform: scale(.75);
  }

  20% {
    transform: scale(1);
  }

  40% {
    transform: scale(.75);
  }

  60% {
    transform: scale(1);
  }

  80% {
    transform: scale(.75);
  }

  100% {
    transform: scale(.75);
  }
}

